  const [selectedMilestoneDueDate, setSelectedMilestoneDueDate] = useState([]);
  const [selectedMilestoneDescriptionType, setSelectedMilestoneDescriptionType] = useState([]);
  const [selectedMilestoneDescription, setSelectedMilestoneDescription] = useState([]);

  const [loaderSpinner, setloaderSpinner] = useState("");
  const [searchInput, setSearchInput] = useState("");
  const [searchResults, setSearchResults] = useState([]);
  const [showlistView, setshowlistView] = useState(false);

  const menuIconBoxRef = useRef(null);
  const boxRef = useRef(null);

  let BearerToken;
  BearerToken = window.localStorage.getItem("Token");
  BearerToken = JSON.parse(BearerToken);
