  //   const selectedWorkspace = combinedData.find((workspace) => workspace.workspaceNames === selectedWorkspaceName);
  //   console.log(selectedWorkspace);

  //   const selectedWorkspaceId = selectedWorkspace ? selectedWorkspace.workspaceId : null;
  //   console.log(selectedWorkspaceId);

  //   // setSelectedWorkspaceId(selectedWorkspaceId);
  // };

  // Refresg Token Functionality
  useEffect(() => {
    const checkAndRefreshToken = async () => {
      const decodedToken = jwt.decode(token);

      // Check if the token is expired or about to expire
      if (decodedToken && decodedToken.exp * 1000 < Date.now() + 300000) {
        setTokenRefreshing(true);

        try {
          const response = await axios.post("https://app-staging.hivelight.com/v1/identities/refresh", {
            meta: {
              token: token,
            },
          });

          // Update the token in your application with the new access token
          const newToken = response.data.meta.token;
          settoken(newToken);
          localStorage.setItem("Token", newToken);
        } catch (error) {
          console.error("Error refreshing token:", error.message);
          localStorage.removeItem("Token");
          location.reload();

          if (error.response && error.response.status === 401) {
            localStorage.removeItem("Token");
            location.reload();
          }
        } finally {
          setTokenRefreshing(false);
        }
      }
    };

    const handleReload = () => {
      checkAndRefreshToken();
    };
    window.addEventListener("beforeunload", handleReload);

    checkAndRefreshToken();

    const tokenCheckInterval = setInterval(() => {
      checkAndRefreshToken();
    }, 300000);

    return () => {
      clearInterval(tokenCheckInterval);
      window.removeEventListener("beforeunload", handleReload);
    };
  }, [token, history]);

  // useEffect(() => {
  //   {
  //     mattersData.map((item, index) => {
  //       // Access the current milestone name within attributes
  //       const currentMilestoneName = item.attributes.currentMilestone.name;
  //       console.log(currentMilestoneName)
  //     })
  //   }
